interface IAnalyzerPageStatusProps {
    id: string;
    url: string;
    threatStatus: BDTLL.PageStatus;
    linkScannerActive: boolean;
}

interface IAnalyzerPageStatusState {
    threatStatus: BDTLL.PageStatus;
    linkScannerActive: boolean;
    title: string;
    text: string;
    icon: BDTLL.StatusIcon;
}

export class AnalyzerPageStatus extends React.Component<IAnalyzerPageStatusProps, IAnalyzerPageStatusState> {
    constructor(props: IAnalyzerPageStatusProps) {
        super(props);
        this.state = this.getPageState(props.threatStatus, props.linkScannerActive) as IAnalyzerPageStatusState;
        if (BDTLL.DEBUG_MODE) {
            console.log("Rendering Page Status...");
        }
    }

    shouldDisplaySearchDisabled(threatStatus: BDTLL.PageStatus, linkScannerActive: boolean) {
        return  threatStatus == BDTLL.PageStatus.SAFE &&
                !linkScannerActive &&
                BDTLL.Utils.get_domain_for_link_scan(this.props.url) != null;
    }

    setPageState(threatStatus: BDTLL.PageStatus, linkScannerActive: boolean) {
        this.setState(this.getPageState(threatStatus, linkScannerActive));
    }

    getPageState(threatStatus: BDTLL.PageStatus, linkScannerActive: boolean) {
        var pageState;

        if (this.shouldDisplaySearchDisabled(threatStatus, linkScannerActive)) {
            pageState = { ...DefaultPageState[BDTLL.PageStatus.SEARCH_ANALYZER_DISABLED] };
        } else {
            pageState = { ...DefaultPageState[threatStatus] };
        }

        return {
            threatStatus: threatStatus,
            linkScannerActive: linkScannerActive,
            ...pageState
        };
    }

    render() {
        let iconClassName: string = "BDTLL_left_box " + this.state.icon;

        const titleTextArray = BDTLL.Utils.get_localized_text("web_protection_title").split("[CompanyName]");
        const firstPartOfText = titleTextArray[0];
        const secondPartOfText = titleTextArray[1];

        return (
            <div id={this.props.id} className="BDTLL_Search_Popup">
                <div className={iconClassName}></div>
                <div className="BDTLL_right_box">
                    <p className="BDTLL_strong BDTLL_title">
                        <b>{this.state.title}</b>
                    </p>
                    <p className="BDTLL_mild BDTLL_description">{this.state.text}</p>
                    <p className="BDTLL_strong BDTLL_web_protection">
                        {firstPartOfText != "" ? <span className="BDTLL_before_company">{firstPartOfText}</span> : null}
                        <span className="BDTLL_company">
                            <b>{BDTLL.Consts.COMPANY_NAME} {BDTLL.Consts.PRODUCT_NAME}</b>
                        </span>
                        {secondPartOfText != "" ? <span className="BDTLL_after_company">{secondPartOfText}</span> : null}
                    </p>
                </div>
            </div>
        )
    }
}
